@import url('https://fonts.googleapis.com/css2?family=Pacifico&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    --light-blue: rgb(240, 240, 255);
}

.header-line {
    width: 100%;
    height: 80px;
    background-color: var(--light-blue);
    box-shadow: 0 2px 2px rgb(127, 127, 127);
    display: flex;
    justify-content: center;
}

header {
    width: 100%;
    max-width: 1200px;
    height: 100%;
}

.header-desktop {
    display: flex;
    height: 100%;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.header-mobile {
    display: none;
    height: 100%;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.header-logo-container {
    width: 70px;
    margin-left: .5rem;
}

.header-logo-container:hover {
    cursor: pointer;
    color: gray;
}

.header-cart-container {
    width: 70px;
    margin-right: .5rem;
}

.header-logo {
    height: 50px;
    width: 50px;
}

.header-title {
    font-family: 'Pacifico', cursive;
    font-size: 1.5rem;
    line-height: 22px;
}
.header-title:hover {
    cursor: pointer;
}

.header-cart {
    height: 50px;
    width: 50px;
    cursor: pointer;
    transition: .1s ease-in-out;
}

.header-cart-label {
    text-align: center;
    font-size: .8rem;
    font-weight: 600;
    letter-spacing: 1px;
    margin-left: 4px;
}

.header-logo-label {
    text-align: center;
    font-size: .8rem;
    font-weight: 600;
    margin-left: 0;
    letter-spacing: 1px;
}

.header-cart-container:hover {
    color: gray;
    cursor: pointer;
}

.header-logo {
    transition: .2s ease-in-out;
}
.header-logo:hover {
    rotate: 135deg;
}

@media screen and (max-width: 600px) {
    .header-desktop {
        display: none;
    }
    .header-mobile {
        display: flex;
    }
}