@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@400;700&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Roboto Condensed', sans-serif;
}

.products-container {
    margin: 3rem 0 3rem;
}

.popular-products-container h2,
.category-products-container h2 {
    font-size: 2rem;
    margin-bottom: 1rem;
    text-transform: capitalize;
    font-weight: 700;
}

.popular-products-container ul {
    display: flex;
    flex-direction: column;
    align-items: center;
    list-style-type: none;
    text-align: left;
}

.popular-products-container ul li {
    width: 80%;
    padding: 3rem 0 3rem;
    border-top: 2px solid rgb(164, 164, 164);
}

.popular-products-container ul li img {
    width: 100%;
    border-radius: 8px;
}

.popular-products-container ul li .title {
    font-size: 2rem;
    margin: .5rem 0;
}

.popular-products-container ul li .title:hover {
    text-decoration: underline;
    cursor: pointer;
    color: gray;
}

.popular-products-container ul li .rating {
    font-size: 1.5rem;
}

.popular-products-container ul li .rating svg {
    color: rgb(255, 255, 63);
}

.popular-products-container ul li .description {
    line-height: 1.3;
    margin: .5rem 0;
}

.popular-products-container ul li .description::first-letter {
    text-transform: capitalize;
}

.popular-products-container ul li .old-price {
    text-decoration: line-through;
    color: rgb(85, 85, 85);
}

.popular-products-container ul li .price {
    font-size: 1.5rem;
    display: flex;
    align-items: center;
}

.popular-products-container ul li .price span {
    font-size: 1rem;
    margin-left: 1rem;
    background-color: orange;
    border-radius: 8px;
    padding: .1rem .5rem;
}

.popular-products-container ul li button {
    margin: 1rem 0 0;
    outline: none;
    padding: .5rem;
    width: 100%;
    font-size: 1.5rem;
    letter-spacing: 2px;
    border: 2px solid rgb(218, 218, 255);
    border-radius: 4px;
    background-color: rgb(240, 240, 255);
    transition: .1s ease-in-out;
}

.popular-products-container ul li button:hover {
    background-color: rgb(218, 218, 255);
    border: 2px solid rgb(194, 194, 255);
    letter-spacing: 1px;
    cursor: pointer;
}



