@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,700;1,400&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.single-product-header {
    display: flex;
    justify-content: left;
    align-items: center;
    flex-direction: row;
}
.single-product-header:hover {
    color: gray;
    text-decoration: underline;
    cursor: pointer;
}

.return-arrow {
    font-size: 2rem;
    margin: .5rem;
    padding: .5rem;
}

.return-text {
    font-size: 1rem;
}

.single-product-content {
    margin: 1rem 2rem;
}

.single-product-title {
    text-transform: capitalize;
    font-size: 2rem;
    text-align: left;
    font-family: 'Playfair Display', serif;
}

.single-product-rating {
    color: rgb(255, 255, 63);
    text-align: left;
    font-size: 1.3rem;
    margin-top: .5rem;
}

.single-product-image-container {
    margin-top: 2rem;
    display: flex;
    align-items: center;
    min-height: 400px;
    background-color: rgb(236, 236, 236);
}

.single-product-image-button-container {
    padding: 1rem;
    width: calc(100% - 4rem);
    position: absolute;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.single-product-image-button {
    border: none;
    outline: none;
    border-radius: 4px;
    padding: 1.5rem 1rem;
    opacity: 70%;
    background-color: rgb(204, 204, 204);
}

.single-product-image {
    width: 100%;
}

.single-product-description {
    padding: 1rem 0;
    display: flex;
    flex-direction: column;
    font-size: 1.2rem;
}

.single-product-description p {
    text-align: left;
    margin: .5rem 0;
}

.single-product-price {
    margin-top: 1rem;
    text-align: left;
}

.single-product-price .old-price {
    text-decoration: line-through;
    color: gray;
    font-size: 1.2rem;
}
.single-product-price .price {
    font-size: 2.5rem;
    font-weight: 700;
}

.single-product-price-button {
    margin: 1rem 0;
    padding: 1rem 0;
    width: 100%;
    outline: none;
    font-size: 2rem;
    letter-spacing: 2px;
    border: 2px solid rgb(218, 218, 255);
    border-radius: 4px;
    background-color: rgb(240, 240, 255);
    transition: .1s ease-in-out;
}

.single-product-price-button:hover {
    background-color: rgb(218, 218, 255);
    border: 2px solid rgb(194, 194, 255);
    letter-spacing: 1px;
    cursor: pointer;
}